body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'ARBONNIE';
  src: url(/static/media/ARBONNIE.08f821af.eot);
  src: local('\263A'),
    url(/static/media/ARBONNIE.228416b7.woff) format('woff'), 
    url(/static/media/ARBONNIE.bea18bdf.ttf) format('truetype'), 
    url(/static/media/ARBONNIE.e3316d22.svg#ARBONNIE) format('svg');
  font-weight: normal;
  font-style: normal;

}
#arTitle {
  font-family: 'ARBONNIE';
  font-size: 150px;
  font-weight: lighter;
  text-align: center;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: auto;
  margin-bottom: auto;
}
#entireThing {
  background-color: #f2f2f2
}
#mainBody {
  white-space: nowrap;
}


#ingredientSearch {
  border-radius: 10px;
  margin-left: 10%;
  margin-right: 10%;
}

#searchField {
  text-align: center;
  overflow: auto;
  line-height: 70%;
  width: 100%;
  height: 40px;
  font-size: 30px;
  border: none;
  background: transparent;
}
#inclusionToggle {
  font-size: 20px;
  margin-left: 10px;
  position: relative;
}
#suggestedIng {
  max-height: 300px;
  list-style-type: none;
  padding-left: 0px;
  margin: 0px;
  overflow-y: auto;
}

.listLabel {
  margin-top: 20px;
  margin-bottom: 0px;
  font-family: 'ARBONNIE';
  font-size: 50px;
}
.listdiv {
  width: 60%;
  overflow-x: hidden;
  margin-left: auto;
  margin-right: auto;
}
.listcard {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 8px;
  float: left;
  margin: 12px;
}
.listcard:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
.listcard:active {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.inclusionCard {
  float: left;
  background: #f1f1f1;
}
.exclusionCard {
  float: left;
  background: red;
  color: white;
}
.cardButton {
  border: none;
  padding: 4px;
  width: 100%;
  height: 30px;
}
.inclusionCardButton {
  background: #cecece;
  color: black;
}
.exclusionCardButton {
  background: rgb(173, 1, 1);
  color: white;
}
#goGuru {
  text-align: center;
  margin-top: 30px;
}
#submit {
  /*filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#f24537', endColorstr='#c62d1f',GradientType=0);*/
  background-color:#f24537;
  border:1px solid #d02718;
  color:#ffffff;
  outline: none;
  /*Customisations*/
  border: 3px solid #ccc;
  padding: 13px;
  border-radius:50px;
  box-shadow: 0 7px #999;
  font-family: 'ARBONNIE';
  font-size: 40px;
  font-weight: bold;
  outline: none;
  cursor: pointer;

}
/*This causes button to appear indented when hovered over*/
#submit:hover {
  background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #c62d1f), color-stop(1, #f24537));
  background:-webkit-linear-gradient(top, #c62d1f 5%, #f24537 100%);
  background:linear-gradient(to bottom, #c62d1f 5%, #f24537 100%);
  filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#c62d1f', endColorstr='#f24537',GradientType=0);
  background-color:#c62d1f;
}
/*This allows button to be pressed visually*/
#submit:active {
  top:1px;
  box-shadow: 0 5px #666;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}
.suggestedItem {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}
.suggestedItem:hover {
  background: grey;
}
.suggestedItem:active {
  background: #ff5454;
}


